.navbar {
  display: flex;
  background: #263238;
  justify-content: space-around;
  width: 100%;
  position: fixed;
  z-index: 100;
  align-items: center;
  text-align: center;
  align-content: center;
  justify-items: center;
}

.navbar > a {
  outline: none;
  text-decoration: none;
  color: white;
  font-weight: bold;
  height: 100%;
  padding: 15px;
  font-family: monospace;
  transition: all 300ms;
}

.navbar > a:hover {
  background: #212121;
}

.navbar > a:active {
  background: white;
  color: black;
}

.title {
  display: flex;
  color: white;
  padding: 0.5rem;
  outline: none;
  text-decoration: none;

  align-items: center;
  justify-items: center;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.title > span {
  width: 10px;
  height: 10px;
  background: orangered;
  border-radius: 50%;
  padding: 0.5rem;
}

.title > p {
  margin-inline: 0.3rem;
}

#link-7 {
  color: transparent;
}

#link-7:hover {
  color: lightgrey;
}

@media screen and (max-width: 425px) {
  .navbar > a:nth-last-child(1) {
    display: none;
  }
  .navbar > a:nth-last-child(2) {
    display: none;
  }
  .navbar > a:nth-last-child(3) {
    display: none;
  }
  .navbar > a:nth-last-child(4) {
    display: none;
  }
  .navbar > a {
    font-size: small;
    padding: 0.5rem;
  }
  .title > span {
    width: 5px;
    height: 5px;
    padding: 0.4rem;
  }
}

@media screen and (max-width: 330px) {
  .title > span {
    width: 5px;
    height: 5px;
    padding: 0.4rem;
  }
}
